body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', 'Roboto', 'Arial', sans-serif;
  background: #FEF7ED;
  color: #44403C;
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}

a {
  color: #C2410C;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}

::-webkit-scrollbar {
  width: 8px;
  background: #FEF7ED;
}
::-webkit-scrollbar-thumb {
  background: #C2410C;
}

@media (max-width: 600px) {
  body, html {
    padding-bottom: 24vw !important;
  }
}
